import io from 'socket.io-client';
import { SERVER_URL } from '~/constants';
export const socket = io(SERVER_URL);
export function emitCallAnswer(dto) {
    socket.emit('call_answer', dto);
}
export function emitCallOffer(dto) {
    socket.emit('call_offer', dto);
}
export function emitChangeLobby(lobbyId) {
    socket.emit('change_lobby', lobbyId);
}
export function emitCreateLobby() {
    socket.emit('create_lobby');
}
export function emitExitPairing() {
    socket.emit('exit_pairing');
}
export function emitFlingBomb(dto) {
    socket.emit('fb', dto);
}
export function emitHoldBomb(dto) {
    socket.emit('hb', dto);
}
export function emitIceCandidate(dto) {
    socket.emit('ice_candidate', dto);
}
export function emitJoinRoom(dto) {
    socket.emit('join_room', dto);
}
export function emitKill(dto) {
    socket.emit('kl', dto);
}
export function emitMove(dto) {
    socket.emit('mv', dto);
}
export function emitMoveBomb(dto) {
    socket.emit('mb', dto);
}
export function emitNullifyBlock(dto) {
    socket.emit('nb', dto);
}
export function emitPlaceBomb(dto) {
    socket.emit('pb', dto);
}
export function emitReady() {
    socket.emit('ready');
}
export function emitSetUser(dto) {
    socket.emit('set_user', dto);
}
