export const dto = {
    call: true,
    fillRoom: true,
    fullScreen: true,
    touchControls: false
};
export default function optionsReducer(state = dto, action) {
    switch (action.type) {
        case 'SET_CALL': return Object.assign(Object.assign({}, state), { call: !state.call });
        case 'SET_FILL_ROOM': return Object.assign(Object.assign({}, state), { fillRoom: !state.fillRoom });
        case 'SET_FULL_SCREEN': return Object.assign(Object.assign({}, state), { fullScreen: !state.fullScreen });
        case 'SET_OPTIONS': return Object.assign({}, action.payload);
        case 'SET_TOUCH_CONTROLS': return Object.assign(Object.assign({}, state), { touchControls: !state.touchControls });
        default: return state;
    }
}
