export const dto = {
    nick: null
};
export default function userReducer(state = dto, action) {
    switch (action.type) {
        case 'SET_USER': return Object.assign({}, action.payload);
        case 'SET_USER_NICK': return Object.assign(Object.assign({}, state), { nick: action.payload });
        default: return state;
    }
}
