import { INITIAL_POSITION, SPEED } from '#/constants';
import { PlayerFactory } from '~/game/entities/player';
export function PlayersFactory(playerDto) {
    const players = playerDto.map((dto, index) => PlayerFactory(Object.assign(Object.assign({}, dto), { index, speed: SPEED, x: INITIAL_POSITION[index][0], y: INITIAL_POSITION[index][1] })));
    return { players, setMyself, tick, render };
}
function setMyself(index) {
    this.myself = this.players[index];
    this.myself.setMyself();
}
function tick(state) {
    this.players.forEach(p => p.tick(state));
}
function render(context) {
    this.players.forEach(p => p.render(context));
}
